






import { defineComponent, PropType } from '@vue/composition-api'
import iconMap from './iconMap'

type IconKey = keyof typeof iconMap

export default defineComponent({
  props: {
    name: {
      type: String as PropType<IconKey>,
      required: true,
    },
  },
  setup(props, context) {
    return {
      context,
      iconMap,
    }
  },
})
