




















import { defineComponent } from '@vue/composition-api'

export default defineComponent({
  props: {
    multiline: {
      type: Boolean,
      default: false,
    },
    value: {
      type: String,
      default: '',
    },
    error: {
      type: Boolean,
      default: false,
    },
  },
  setup(_, context) {
    const onInput = (value: string) => context.emit('input', value)
    return {
      context,
      onInput,
    }
  },
})
