




















import { defineComponent, PropType } from '@vue/composition-api'

export default defineComponent({
  props: {
    label: {
      type: String,
      default: '',
    },
    subLabel: {
      type: String,
      default: '',
    },
    errorMessage: {
      type: String,
      default: '',
    },
    required: {
      type: Boolean as PropType<boolean | null>,
      default: null,
    },
  },
  setup(props, context) {
    return {
      context,
    }
  },
})
