

























import { defineComponent, PropType } from '@vue/composition-api'
import Icon from '@/components/Icon/Icon.vue'

export default defineComponent({
  components: {
    Icon,
  },
  props: {
    skin: {
      type: String as PropType<
        | 'primary'
        | 'primary-line'
        | 'secondary'
        | 'tertiary'
        | 'muted'
        | 'disabled'
      >,
      default: 'primary',
    },
    size: {
      type: String as PropType<'xl' | 'l' | 'm' | 's' | 'xs'>,
      default: 'm',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    tag: {
      type: String,
      default: 'button',
    },
  },
  setup(props, context) {
    return {
      context,
    }
  },
})
