













import { defineComponent } from '@vue/composition-api'
import Icon from '@/components/Icon/Icon.vue'

export default defineComponent({
  components: {
    Icon,
  },
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  setup(_, context) {
    const onInput = (checked: boolean) => {
      if (checked) {
        context.emit('input', true)
      }
    }
    return {
      context,
      onInput,
    }
  },
})
