
















import { defineComponent } from '@vue/composition-api'

export default defineComponent({
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, context) {
    const onInput = (value: string) => context.emit('input', value)
    return {
      onInput,
      context,
    }
  },
})
